* {
    box-sizing: border-box;
}

body {
    display: flex;
    flex-direction: column;
    background-image: url('./components/images/green-back.jpg');
    /* Image from freepik */
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    font-family: 'Open Sans', sans-serif;

}

header {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px 20px;
}

.header--logo {
    width: 50px;
    max-width: 50px;
}

.header--logo-title {
    margin-left: 7px;
    font-family: 'Lobster', cursive;
    font-weight: normal;
    color: white;
}

main {
    text-align: center;
    color: White;
    text-shadow: 2px 2px 4px #000000;
    margin-top: 50px;
}

.message-el {
    font-style: italic;
}

.main--play-area {
    margin: 50px auto;
}

.main--buttons-container {
    display: grid;
    grid: auto/ auto;
    margin-top: 50px;
    padding: 0px 100px;
    gap: 5px;
} 

button {
    border: none;
    font-size: 1em;
    font-weight: 600;
    color: #006858;
    background-color: white;
    padding: 20px 30px;
    border-radius: 3px;
}

button:hover {
    cursor: pointer;
    filter:brightness(90%)
}

footer {
    font-family: 'Source Sans Pro';
    font-size: .75em;
    font-weight: light;
    text-align: center;
    margin-top: 100px;
    padding-top: 15px;
    padding-bottom: 15px;
    position: relative;
    bottom: 0;
    left: 0;
    width: 100%;
    color: white;
}

.footer-icons {
    width: 23px;
    margin-right: 13px;
    filter: invert(100%);
}

.footer-icons:hover{
    filter: invert(30%);
}

footer > p {
    line-height: .5;
}

@media screen and (min-width: 500px) {
    .main--buttons-container {
        padding: 0 300px;
    }

    footer {
        position: fixed;
        padding-bottom: 60px;
    }
}

@media screen and (min-width: 1025px) {
    header {
        padding: 10px 300px;
    }

    .main--play-area {
        margin: 45px auto;
    }
    
    .main--buttons-container {
        padding: 0 850px;
    }

    button {
        padding: 20px 60px;
    }

    footer {
        position: fixed;
        font-size: .65em;
        padding-bottom: 5px;
    }

    .footer-icons {
        width: 20px;
    }
}